* {
    margin: 0;
    padding: 0;
}

.container {
    margin: auto;
    width: 1200px;
    display: flex;
    flex-direction: column;
}

#inner {
    max-width: 100%;
    min-height: 50vh;
    background: linear-gradient(90deg, #000000 50%, rgba(125, 186, 232, 0) 100%), url(https://rcc-sport.ru/local/templates/main/img/bg-red.png) top right, #121212;
}

.content {
    display: flex;
    flex-direction: column;
    padding: 7%;
    font-family: Arial, Helvetica, sans-serif;
}

.content h1 {
    color: white;
    text-transform: uppercase;
    font-size: 40px;
}

.links {
    color: rgba(255, 255, 255, 0.562);
    margin-top: 10%;
    display: flex;
    font-size: 14px;
}

main {
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.training {
    display: flex;
    gap: 20px;
    flex-direction: row;
    justify-content: center;
}


.subscription,
.personally {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    color: white;
    width: 400px;
    height: 250px;
    font-family: Arial, Helvetica, sans-serif;
    border-radius: 5px;
    padding: 30px;
    font-size: 20px;
    background: linear-gradient(130deg, #000000 30%, rgba(125, 186, 232, 0) 100%), url(https://rcc-sport.ru/local/templates/main/img/bg-red.png) top right, #121212;
}

#head-price {
    display: flex;
    justify-content: space-between;
}

#head-price h4 {
    background-color: rgba(192, 192, 192, 0.47);
    padding: 10px;
    border: 5px;
}

#month-price {
    display: flex;
    justify-content: flex-start;
    gap: 10px;
}